<template>
  <!-- toast -->
  <Toast :breakpoints="{ '414px': { width: '100%', right: '0', left: '0' } }" />
  <div
    class="modal fade small"
    id="productModal"
    tabindex="-1"
    data-bs-backdrop="static"
    data-bs-keyboard="false"
    aria-labelledby="exampleModalLabel"
    aria-hidden="true"
    ref="bsModal"
  >
    <div
      class="modal-dialog modal-dialog-centered modal-dialog-scrollable modal-lg"
    >
      <div class="modal-content">
        <!-- header -->
        <div class="modal-header bg-primary text-white">
          <h6 class="modal-title" id="exampleModalLabel">
            <span v-if="isNew"></span>
            <span v-else></span>
          </h6>
          <button
            type="button"
            class="btn-close"
            data-bs-dismiss="modal"
            aria-label="Close"
          ></button>
        </div>
        <Form v-slot="{ errors }" @submit="emitUpdateProduct">
          <!-- body -->
          <div class="modal-body">
            <div class="row">
              <!--  -->
              <div class="col-md-5 col-lg-4">
                <div class="mb-3">
                  <label class="form-label"></label>
                  <input
                    type="text"
                    class="form-control"
                    placeholder=""
                    v-model="tempProduct.image"
                  />
                </div>
                <div class="mb-3">
                  <label class="form-label"
                    > 
                    <span v-show="fileUploading">
                      <i class="fa-solid fa-spinner fa-spin-pulse"></i>
                    </span>
                  </label>
                  <input
                    class="form-control"
                    type="file"
                    ref="files"
                    @change="uploadFile"
                  />
                </div>
                <img
                  class="img-fluid rounded"
                  :src="tempProduct.image"
                  alt=""
                />
              </div>
              <!--  -->
              <div class="col-md-7 col-lg-8">
                <div class="row">
                  <div class="col-6">
                    <div class="mb-3">
                      <label class="form-label"
                        > <span class="text-danger">*</span>
                      </label>
                      <Field
                        name=""
                        type="text"
                        class="form-control"
                        :class="{ 'is-invalid': errors[''] }"
                        placeholder=""
                        rules="required"
                        v-model="tempProduct.title"
                      />
                      <ErrorMessage name="" class="invalid-feedback" />
                    </div>
                  </div>
                  <div class="col-6">
                    <div class="mb-3">
                      <label class="form-label"></label>
                      <select
                        class="form-select"
                        v-model="tempProduct.category"
                      >
                        <option value="" disabled></option>
                        <option value=""></option>
                        <option value=""></option>
                      </select>
                    </div>
                  </div>
                  <div class="col-6">
                    <div class="mb-3">
                      <label class="form-label"></label>
                      <select
                        class="form-select"
                        v-model="tempProduct.description"
                      >
                        <option value="" disabled></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                        <option value=""></option>
                      </select>
                    </div>
                  </div>
                  <div class="col-6">
                    <div class="mb-3">
                      <label class="form-label"></label>
                      <select class="form-select" v-model="tempProduct.content">
                        <option value="" disabled></option>
                        <option value=""></option>
                        <option value=""></option>
                      </select>
                    </div>
                  </div>
                  <div class="col-6">
                    <div class="mb-3">
                      <label class="form-label"></label>
                      <input
                        type="number"
                        class="form-control"
                        placeholder=""
                        v-model="tempProduct.origin_price"
                      />
                    </div>
                  </div>
                  <div class="col-6">
                    <div class="mb-3">
                      <label class="form-label"></label>
                      <input
                        type="number"
                        class="form-control"
                        placeholder=""
                        v-model="tempProduct.price"
                      />
                    </div>
                  </div>
                </div>
                <div class="form-check form-switch">
                  <input
                    class="form-check-input"
                    type="checkbox"
                    role="switch"
                    id="flexSwitchCheckDefault1"
                    v-model="tempProduct.is_enabled"
                  />
                  <label class="form-check-label" for="flexSwitchCheckDefault1"
                    ></label
                  >
                </div>
                <div class="form-check form-switch">
                  <input
                    class="form-check-input"
                    type="checkbox"
                    role="switch"
                    id="flexSwitchCheckDefault2"
                    v-model="tempProduct.unit"
                  />
                  <label class="form-check-label" for="flexSwitchCheckDefault2"
                    ></label
                  >
                </div>
              </div>
            </div>
          </div>
          <!-- footer -->
          <div class="modal-footer">
            <button
              type="button"
              class="btn btn-sm btn-outline-secondary"
              data-bs-dismiss="modal"
            >
              
            </button>
            <button type="submit" class="btn btn-sm btn-primary"></button>
          </div>
        </Form>
      </div>
    </div>
  </div>
</template>
<script>
import toggleModal from "@/js/mixins/toggleModal";
export default {
  props: ["product", "isNew"],
  emits: ["updateProduct"],
  mixins: [toggleModal],
  data() {
    return {
      tempProduct: {},
      fileUploading: false,
    };
  },
  methods: {
    //  tempProduct  Products.vue
    emitUpdateProduct() {
      this.$emit("updateProduct", this.tempProduct);
    },
    // 
    uploadFile() {
      const uploadedFile = this.$refs.files.files[0];
      // 
      const formData = new FormData();
      formData.append("file-to-upload", uploadedFile);
      //
      this.fileUploading = true;
      const api = "https://vue-course-api.hexschool.io/api/yunhsi/admin/upload";
      this.axios
        .post(api, formData, {
          headers: {
            "Content-Type": "multipart/form-data",
          }, //  formData  form-data  api
        })
        .then((res) => {
          if (res.data.success) {
            this.tempProduct.image = res.data.imageUrl;
            this.fileUploading = false;
            this.showSuccessMsg("");
          } else {
            this.showErrorMsg(res.data.message.message);
            this.fileUploading = false;
          }
        })
        .catch((err) => {
          this.$swal({
            icon: "error",
            title: `${err}`,
          });
        });
    },
    // 
    showSuccessMsg(msg) {
      this.$toast.add({
        severity: "success",
        summary: "",
        detail: `${msg}`,
        life: 1000,
      });
    },
    // 
    showErrorMsg(msg) {
      this.$toast.add({
        severity: "error",
        summary: "",
        detail: `${msg}`,
        life: 1000,
      });
    },
    // 
    clear() {
      this.$toast.removeAllGroups();
    },
  },
  watch: {
    //  product  tempProduct 
    product() {
      this.tempProduct = this.product;
    },
  },
};
</script>
<style lang="scss" scoped>
.form-switch {
  input,
  label {
    cursor: pointer;
  }
}
