<template>
  <!-- loader -->
  <loading v-model:active="isLoading" />
  <!-- toast -->
  <Toast :breakpoints="{ '414px': { width: '100%', right: '0', left: '0' } }" />
  <!-- navbar -->
  <Navbar :filterItem="filterItem" @changeKeyword="changeKeyword" />
  <div class="d-flex justify-content-between">
    <!-- title -->
    <h1 class="h3 m-0 d-flex align-items-center"></h1>
    <!--  -->
    <button
      type="button"
      class="btn btn-sm btn-primary d-block d-flex align-items-center"
      @click="openModal(true)"
    >
      <span class="material-icons pe-2">add_circle</span>
    </button>
  </div>
  <!--  -->
  <p class="text-end pt-3 small">
    
    <select v-model="sortType">
      <option v-for="(item, index) in sortItem" :key="index" :value="index">
        {{ item }}
      </option>
    </select>
    
  </p>
  <!--  -->
  <Transition v-if="isReady" name="fade">
    <div class="pt-3" v-show="filterKeyword.length">
      <table class="table table-hover align-middle">
        <!--  -->
        <thead>
          <tr>
            <th scope="col"></th>
            <th class="text-center" scope="col"></th>
            <th class="text-center" scope="col"></th>
            <th class="text-center" scope="col"></th>
            <th class="text-center" scope="col"></th>
          </tr>
        </thead>
        <!--  -->
        <tbody class="small">
          <tr v-for="item in filterKeyword" :key="item.id">
            <th scope="row">{{ item.title }}</th>
            <td class="text-center">{{ item.percent }}%</td>
            <td class="text-center">
              {{ item.due_date ? item.due_date : "-" }}
            </td>
            <td class="text-center">
              <span v-if="item.is_enabled" class="text-success material-icons"
                >check_circle</span
              >
              <span v-else></span>
            </td>
            <!--  -->
            <td class="text-center">
              <div class="d-grid gap-2 d-md-block">
                <button
                  class="btn btn-sm btn-outline-primary"
                  type="button"
                  @click="openModal(false, item)"
                >
                  
                </button>
                <button
                  class="btn btn-sm btn-outline-danger"
                  type="button"
                  @click="deleteConfirm(item.id)"
                >
                  
                </button>
              </div>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </Transition>
  <!-- no data -->
  <Transition v-if="isReady" name="fade">
    <NoData v-show="!filterKeyword.length" :keyword="keyword" />
  </Transition>
  <!--  -->
  <Pagination
    :pagination="pagination"
    @getList="getCoupons"
    v-show="pagination.current_page && filterKeyword.length"
  />
  <!-- couponModal -->
  <CouponModal
    :coupon="tempCoupon"
    :isNew="isNew"
    @updateCoupon="updateCoupon"
    ref="couponModal"
  />
</template>

<script>
import Navbar from "@/components/admin/Navbar";
import Pagination from "@/components/widget/Pagination";
import CouponModal from "@/components/admin/CouponModal";
import NoData from "@/components/widget/NoData";
export default {
  components: {
    Navbar,
    Pagination,
    CouponModal,
    NoData,
  },
  data() {
    return {
      isLoading: false,
      coupons: [],
      pagination: {},
      isNew: false,
      tempCoupon: {},
      isReady: false,
      // 
      filterItem: {
        placeholder: "",
      },
      // 
      keyword: "",
      // 
      sortItem: {
        0: "",
        1: "",
        2: "",
      },
      // 
      sortType: "0",
    };
  },
  computed: {
    // 
    filterKeyword() {
      if (this.keyword) {
        // 
        const data = this.coupons.filter((item) => {
          let content = item.title.toLowerCase();
          let keyword = this.keyword.toLowerCase();
          return content.match(keyword);
        });
        // 
        if (this.sortType) {
          data.sort((t1, t2) => {
            let a = new Date(t1.due_date);
            let b = new Date(t2.due_date);
            return this.sortType == 1 ? b - a : a - b;
          });
        }
        return data;
      } else {
        // 
        const data = this.coupons;
        // 
        if (this.sortType) {
          data.sort((t1, t2) => {
            let a = new Date(t1.due_date);
            let b = new Date(t2.due_date);
            return this.sortType == 1 ? b - a : a - b;
          });
        }
        return data;
      }
    },
  },
  mounted() {
    this.getCoupons();
  },
  methods: {
    //  modal
    openModal(isNew, item) {
      this.$refs.couponModal.showModal();
      if (isNew) {
        // 
        this.tempCoupon = {};
        this.isNew = true;
      } else {
        // 
        this.tempCoupon = Object.assign({}, item); // 
        this.isNew = false;
      }
    },
    // 
    getCoupons(page = 1) {
      this.isLoading = true;
      const api = `https://vue-course-api.hexschool.io/api/yunhsi/admin/coupons?page=${page}`;
      this.axios
        .get(api)
        .then((res) => {
          if (res.data.success) {
            this.isLoading = false;
            this.coupons = res.data.coupons;
            this.pagination = res.data.pagination;
            this.$refs.couponModal.hideModal();
          }
        })
        .catch((err) => {
          this.$swal({
            icon: "error",
            title: `${err}`,
          });
        });
    },
    //  or 
    updateCoupon() {
      this.isLoading = true;
      let api = "";
      let httpMethod = "";
      if (this.isNew) {
        // 
        api = "https://vue-course-api.hexschool.io/api/yunhsi/admin/coupon";
        httpMethod = "post";
      } else {
        // 
        api = `https://vue-course-api.hexschool.io/api/yunhsi/admin/coupon/${this.tempCoupon.id}`;
        httpMethod = "put";
      }
      //  tempCoupon  api
      this.axios[httpMethod](api, { data: this.tempCoupon })
        .then((res) => {
          if (res.data.success) {
            this.getCoupons();
            setTimeout(() => {
              this.showSuccessMsg(res.data.message);
            }, 500);
            this.$refs.couponModal.hideModal();
          }
        })
        .catch((err) => {
          this.$swal({
            icon: "error",
            title: `${err}`,
          });
        });
    },
    // 
    showSuccessMsg(msg) {
      this.$toast.add({
        severity: "success",
        summary: "",
        detail: `${msg}`,
        life: 1000,
      });
    },
    // 
    showErrorMsg(msg) {
      this.$toast.add({
        severity: "error",
        summary: "",
        detail: `${msg}`,
        life: 1000,
      });
    },
    // 
    clear() {
      this.$toast.removeAllGroups();
    },
    // 
    deleteConfirm(id) {
      this.$swal({
        title: "?",
        text: "",
        icon: "warning",
        showCancelButton: true,
        confirmButtonColor: "#0D6EFB",
        cancelButtonColor: "#dc3534",
        confirmButtonText: "",
        cancelButtonText: "",
      }).then((result) => {
        if (result.isConfirmed) {
          this.deleteCoupon(id);
        }
      });
    },
    // 
    deleteCoupon(id) {
      this.isLoading = true;
      const api = `https://vue-course-api.hexschool.io/api/yunhsi/admin/coupon/${id}`;
      this.axios
        .delete(api)
        .then((res) => {
          if (res.data.success) {
            this.getCoupons();
            this.showSuccessMsg(res.data.message);
          }
        })
        .catch((err) => {
          this.$swal({
            icon: "error",
            title: `${err}`,
          });
        });
    },
    changeKeyword(keyword) {
      this.keyword = keyword;
    },
  },
  watch: {
    // 
    coupons() {
      this.isReady = true;
    },
    keyword() {
      this.isReady = true;
    },
  },
};
</script>

<style></style>
